@import "properties";

* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

main {
  background-color: $background-gray;
  margin-top: 60px;

  .container {
    display: flex;
    justify-content: left;
    margin: 0 auto;
    max-width: 1000px;
  }

  a {
    text-decoration: none;
    color: $link-color;
    font-size: 0.9em;
    font-weight: 500;
  }
}

@media only screen and (max-width: 1024px) {
  main .container {
    justify-content: center !important;
  }
}
